@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400&display=swap');


a, a:hover {
  color: inherit;
  text-decoration: none;
}

.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px
}

.error-message {
  color: red;
  padding: 10px
}

.footertext{
  display: grid;
    grid-template-columns: 33% 33% 33%;
    grid-template-rows: 100px;
    text-align: center;
    align-items: center;
    background-color: #333333;
    color: white;
}

.about .write .follow{
font-family: 'Playfair Display', serif;
color:whitesmoke
}

#page-container {
  position: relative;
  min-height: 100vh;
}

#content-wrap {
  padding-bottom: 2.5rem;    /* Footer height */
}

#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;            /* Footer height */
}
.follow{
    display: flex;
    justify-content: center;
}